import React, { useEffect, useState } from "react";
import LoadingScreen from "../../pages/LoadingScreen";
import { Link, useLocation, useNavigate, useParams } from "react-router-dom";   
import Success from "./success";
import { GET_PAYMENT_RESPONSE, STRIPE_CHECKOUT } from "../../../actions/planDetail";
import { useDispatch } from "react-redux";

export default function StripeCallback() { 

  const navigate = useNavigate(); 
  const dispatch = useDispatch()
  const location = useLocation();


  useEffect(() => {
    const session_id = new URLSearchParams(location.search).get("session_id");
    STRIPE_CHECKOUT(session_id).then((response) => {
      const { response_code, responseData } = response.data
      if(response_code == 200){
        dispatch(GET_PAYMENT_RESPONSE(responseData[0].referenceId, navigate))
        }
  })
  }, [])


  return ( <LoadingScreen /> )

}